<template>
  <div class="tableProducts">
    <Header />
    <div class="body">
      <div v-for="(product, index) in $store.state.ventas" :key="index">
        <div class="flex">
          <div class="first-container">
            <font-awesome-icon
              @click="minus(index, product.id)"
              icon="minus"
              :style="{ border: '1px solid #ddd', color: '#aaa', cursor: 'pointer', paddingLeft: 10, paddingBottom: '.25em', paddingTop: '.25em', paddingRight: 5 }"
              size="xs"
            />
            <p class="qty">{{testQty(product)}}</p>
            <font-awesome-icon
              @click="more(index, product.id)"
              icon="plus"
              :style="{ border: '1px solid #ddd', color: '#aaa', cursor: 'pointer', paddingLeft: 5, paddingBottom: '.25em', paddingTop: '.25em', paddingRight: 10 }"
              size="xs"
            />
          </div>
          <div class="flex-icons">
            <font-awesome-icon
              v-show="isDown(product)"
              @click="toggleAdditions(index)"
              icon="chevron-down"
              :style="{ color: '#aaa', cursor: 'pointer', paddingBottom: 10, paddingTop: 10, paddingRight: 5 }"
              size="lg"
            />
          </div>
          <p :class="{'icon.down': isDown(product), 'name-product': true}">{{fixName(product.name)}}</p>
          <p class="price">{{getTotalProduct(product)}}</p>
          <font-awesome-icon
            @click="deleteProductLocal(product, index)"
            icon="times-circle"
            :style="{ color: '#ddd', cursor: 'pointer', padding: 12 }"
            size="lg"
          />
        </div>
        <div v-if="isAdditions(product, index)">
          <div
            v-for="(addition, index2) in additionsActive(product.additions)"
            :key="index2"
            class="adds"
          >
            <div class="left">
              <div class="minus" @click="removeAddition(product, addition, index, index2)">-</div>
              {{addition.qty}}
              {{addition.name}}
            </div>
            <div class="right">{{numberWithCommas(getAdditionsPrice(addition))}}</div>
          </div>
          <div
            v-for="(modification, index2) in additionsActive(product.modifications)"
            :key="index2"
            class="adds"
          >
            <div class="left">
              <div class="minus">-</div>
              {{modification.qty}}
              {{modification.nombre}}
            </div>
          </div>
        </div>
      </div>
    </div>
    <Footer />
    <div class="flex-container pad-left" v-if="$store.state.warehouse.domicilios">
      <div class="boton domicilios-container width20" @click="openDomicilios()">
        <p class="pagar">Domicilios</p>
      </div>
      <div class="boton domicilios-container width20" @click="openNota()">
        <p class="pagar">Nota</p>
      </div>
      <div class="boton width60 flex-boton" @click="openModal()" v-if="!$store.state.statusComanda">
        <p class="pagar">Pagar</p>
      </div>
      <div class="boton width60 flex-boton" @click="sendCommanda()" v-else>
        <font-awesome-icon
          icon="print"
          :style="{ color: '#fff', cursor: 'pointer', paddingRight: 10 }"
          size="2x"
        />
        <p class="pagar">Pagar / Comanda</p>
      </div>
    </div>
    <div class="flex-container pad-left" v-else>
      <div class="boton domicilios-container width20" @click="openNota()">
        <p class="pagar">Nota</p>
      </div>
      <div class="boton width80 flex-boton" @click="openModal()" v-if="!$store.state.statusComanda">
        <p class="pagar">Pagar</p>
      </div>
      <div class="boton width80 flex-boton" @click="sendCommanda()" v-else>
        <font-awesome-icon
          icon="print"
          :style="{ color: '#fff', cursor: 'pointer', paddingRight: 10 }"
          size="2x"
        />
        <p class="pagar">Pagar / Comanda</p>
      </div>
    </div>

    <Domicilios />
    <ModalPago />
    <Descuento />
    <Propina />
    <DescuentoByProduct :index="index" />
    <Leal />
    <sweet-modal ref="input">
      <h4>Nota comanda:</h4>
      <input class="input-item" type="text" id="nota-comanda" v-model="nota_comanda" />
      <div>
        <button
          @click="agregarNotaComanda()"
          style="margin-left: auto;margin-right: auto;"
          class="button button-success"
        >Continuar</button>
      </div>
    </sweet-modal>
    <sweet-modal ref="input2">
      <h4>Abrir caja:</h4>
      <input class="input-item" type="number" min="0" max="999999999" id="open-box" v-model="open_box" />
      <div>
        <button
          @click="openBox()"
          style="margin-left: auto;margin-right: auto;"
          class="button button-success"
        >Continuar</button>
      </div>
    </sweet-modal>
  </div>
</template>
<script>
import helperCommas from "../../helpers/commas";
import helperVentas from "../../helpers/ventas";
import helperConfig from "../../helpers/config";
import helperAlert from "../../helpers/alert";
import axios from "axios";
import _ from "lodash";

import Leal from "../Leal";
import Domicilios from "../Domicilios";
import ModalPago from "../ModalPago";
import Descuento from "../Descuento";
import Propina from "../Propina";
import DescuentoByProduct from "./DescuentoByProduct";
import { mapMutations } from "vuex";

import Header from "./Header";
import Footer from "./Footer";
export default {
  name: "TableProducts",
  components: {
    Header,
    Footer,
    ModalPago,
    Descuento,
    Propina,
    DescuentoByProduct,
    Domicilios,
    Leal
  },
  data() {
    return {
      isLoading: false,
      index: -1,
      products: [{ name: "test", qty: 1, total: 1000 }],
      statusAdditions: -1,
      additions: [],
      nota_comanda: "",
      comanda: {},
      open_box: 0,
    };
  },
  methods: {
    ...mapMutations([
      "deleteProduct",
      "addProduct",
      "incrementProduct",
      "decrementProduct",
      "discountProduct"
    ]),
    async getDomiciliaries() {
      try {
        const res = await axios(
          this.getConfig("get", "/take-order/domiciliaries")
        );
        let data = res.data.data;
        this.$store.state.domicilios = data;
      } catch (error) {
        //
      }
    },
    openDomicilios() {
      this.$modal.show("domicilios");
    },
    toggleDiscountByProduct(index) {
      if (!this.getStatusDiscountGlobal()) {
        this.index = index;
        this.$modal.show("discount-by-product");
      } else {
        this.setAlert("Solo puede agregar un tipo de descuento");
      }
    },
    testQty(product) {
      return product.price.qty;
    },
    getDescuento(addition) {
      if (this.$store.state.discount.status) {
        return addition.discount ? addition.discount : 0;
      }
      return 0;
    },
    getAdditionsPrice(addition) {
      return (
        addition.qty * addition.price -
        this.getDescuento(addition) * addition.qty
      );
    },
    isAdditions(product, index) {
      if (product)
        return (
          this.statusAdditions === index &&
          product.additions &&
          product.additions.length > 0
        );
      return false;
    },
    additionsActive(additions) {
      return additions && additions.length > 0
        ? additions.filter(function(a) {
            return a && a.status === 1;
          })
        : [];
    },
    removeAddition(product, item, index, index2) {
      if (!item) {
        return;
      }
      if (item.name) {
        let add_aux = Object.assign({}, this.$store.state.ventas);
        if (add_aux[index].additions[index2].qty <= 1) {
          add_aux[index].additions[index2].qty = 0;
          add_aux[index].additions[index2].status = 0;
        } else {
          add_aux[index].additions[index2].qty -= 1;
        }

        this.$store.state.ventas = [
          ...this.$store.state.ventas.filter((element, i) => {
            return i !== index;
          }),
          add_aux[index]
        ];
      } else {
        let add_aux = Object.assign({}, this.$store.state.ventas);

        if (add_aux[index].modifications[index2].qty <= 1) {
          add_aux[index].modifications[index2].qty = 0;
          add_aux[index].modifications[index2].status = 0;
        } else {
          add_aux[index].modifications[index2].qty -= 1;
        }
        add_aux[index];
        this.$store.state.ventas = [
          ...this.$store.state.ventas.filter((element, i) => {
            return i !== index;
          }),
          add_aux[index]
        ];
      }
    },
    more(index, id) {
      this.incrementProduct({ index, id });
    },
    minus(index, id) {
      this.decrementProduct({ index, id });
    },
    isDown(item) {
      return item.additions && item.additions.length > 0;
    },
    toggleAdditions(index) {
      if (this.statusAdditions !== index) this.statusAdditions = index;
      else this.statusAdditions = -1;
    },
    hasProduct() {
      return this.$store.state.ventas.length > 0;
    },
    compareProducts(a, b) {
      return (
        _.isEqual(_.sortBy(a.additions), _.sortBy(b.additions)) &&
        _.isEqual(_.sortBy(a.modifications), _.sortBy(b.modifications))
      );
    },
    fixGetIdProducts() {
      // Function get products repeated and add qty
      let aux_products = this.getIdProducts();
      let products = [];
      aux_products.map(product => {
        let index = products.findIndex(
          i_product => i_product.id === product.id
        );
        if (index !== -1) {
          let compare_additions = _.isEqual(
            _.sortBy(products[index].additions),
            _.sortBy(product.additions)
          );
          let compare_modifications = _.isEqual(
            _.sortBy(products[index].modifications),
            _.sortBy(product.modifications)
          );
          if (compare_additions && compare_modifications) {
            products[index].additions = [
              ...products[index].additions,
              ...product.additions
            ];
            products[index].modifications = [
              ...products[index].modifications,
              ...product.modifications
            ];
          } else {
            products.push(product);
          }
        } else {
          products.push(product);
        }
      });
      return products;
    },
    statusDomicilio() {
      let domicilio = this.$store.state.domicilioSelected.id;

      if (!domicilio) {
        return true;
      }

      return (
        this.$store.state.userSelected !== "" &&
        this.$store.state.userSelected.phone !== "" &&
        this.$store.state.userSelected.address !== ""
      );
    },
    async sendCommanda() {
      if (this.statusDomicilio()) {
        if (!this.isLoading) {
          if (this.hasProduct()) {
            //this.isLoading = true
            let note = this.$store.state.note;
            let products = this.getIdProducts();
            let data = {
              zone: '-1',
              table: '-1',
              products: products,
              note: note
            };
            this.enviarComanda(data);
            /** ---------------------------------------- */
          } else {
            this.setAlert("Por favor agregue productos");
          }
        }
      } else {
        this.setAlert(
          "Debe llenar todos los datos de entrega ya que selecciono un domicilio"
        );
      }
    },
    agregarNotaComanda() {
      this.$store.state.note = this.nota_comanda;
      this.$refs.input.close();
    },
    async openBox() {
      if (!isNaN(this.open_box) && this.open_box > 0 && this.open_box <= 99999999) {
        try {
          await axios(this.getConfig("post", "/box/open", {
            value: this.open_box,
          }));
          this.open_box = 0;
          this.$refs.input2.close();
          this.Continue();
        } catch (error) {
          this.open_box = 0;
          this.$refs.input2.close();
          this.setAlert("Error al intentar realizar la apertura de caja");
        }
      } else {
        this.open_box = 0;
        this.$refs.input2.close();
        this.setAlert("Para realizar la apertura de caja debe digitar un número entre 1 y 99.999.999");
      }
    },
    async enviarComanda(data) {
      try {
        await axios(this.getConfig("post", "/take-order/order/command", data));
        this.isLoading = false;
        this.openModal();
      } catch (error) {
        //
      }
    },
    fixName(name) {
      name = name.length > 20 ? name.substring(0, 20) : name;
      name = name.toUpperCase();

      return name;
    },
    getTotalProduct(product) {
      let total = this.getTotalByProduct(product);

      return this.numberWithCommas(total);
    },
    async Continue() {
      let ids = this.$store.state.ventas;
      if (ids.length > 0) {
        let total = this.getTotalFixed();
        this.$store.state.total = total;
        this.$store.state.totalModalpago = parseInt(
          parseFloat(total)
            .toFixed(this.$store.state.data_currency.decimals)
            .toString()
            .replace(".", "")
        );
        this.$modal.show("hello-world");
      } else {
        this.setAlert("Por favor Agrega productos");
      }
    },
    openModal() {
      if (this.statusDomicilio()) {
        if (this.hasProduct()) {
          this.getStatusApertura();
        } else {
          this.setAlert("Por favor Agrega productos");
        }
      } else {
        this.setAlert(
          "Debe llenar todos los datos de entrega ya que selecciono un domicilio"
        );
      }
    },
    openNota() {
      this.nota_comanda = this.$store.state.note;
      this.$refs.input.open();
    },
    async getStatusApertura() {
      try {
        const res = await axios(this.getConfig("get", "/box/verify-state"));
        let status = res.data.status;
        if (status) {
          this.Continue();
        } else {
          this.$refs.input2.open();
        }
      } catch (error) {
        //
      }
    },
    deleteProductLocal(product, index) {
      this.deleteProduct({ product, index });
    },
    setAlert: helperAlert.setAlert,
    getConfig: helperConfig.getConfig,
    numberWithCommas: helperCommas.numberWithCommas,
    getTotalFixed: helperVentas.getTotalFixed,
    getIdProducts: helperVentas.getIdProducts,
    getTotalByProduct: helperVentas.getTotalByProduct,
    getStatusDiscountGlobal: helperVentas.getStatusDiscountGlobal
  },
  mounted() {
    this.getDomiciliaries();
  }
};
</script>

