<template>
<div class="container-carousel">
  <div class="flex-domicilios">
    <div class="left-domicilios">
        <font-awesome-icon
          icon="caret-up"
          :style="{ color: '#505050', cursor: 'pointer'}"
          size="4x"
          @click="prev()"
        />  
    </div>
    <div class="right-domicilios">
        <font-awesome-icon
          icon="caret-down"
          :style="{ color: '#505050', cursor: 'pointer'}"
          size="4x"
          @click="next()"
        />  
    </div>
  </div>
  <div class="subcontainer-carousel">
    <slick
      ref="slick"
      :options="slickOptions"
      @init="handleInit"
      @reInit="handleReInit"
    >
      <div v-for="(domicilio, index) in getDomicilios" :key="index" class="slide">
        <div
          class="domicilio-item__container"
          @click="selectDomicilio(domicilio)"
        >
          <div :class="{'domicilio-active':
            $store.state.domicilioSelected.id === domicilio.id}"
          >
            <img 
              :alt="domicilio.description"
              class="image"
              @click="selectDomicilio(domicilio)"
              :src="fixUrl(domicilio.logo)"
              @error="replaceByDefault"
            >
            <div class="name">{{domicilio.description}}</div>
          </div>
        </div>
      </div>
    </slick>
  </div>
</div>
</template>

<script>
import helperConfig from "../../helpers/config";
import Slick from 'vue-slick';

export default {
  name: 'Carrusel',
  components: { Slick },
  data() {
    return {
      selected: {},
      slickOptions: {
        slidesToShow: 3,
        vertical: true,
        infinite: true,
        adaptiveHeight: true,
        dots: false,
        arrows: false
      },
    }
  },
  methods: {
    next() {
      this.$refs.slick.next();
    },
    prev() {
      this.$refs.slick.prev();
    },
    reInit() {
      this.$nextTick(() => {
        this.$refs.slick.reSlick();
      })
    },
    handleInit(event, slick) {},
    handleReInit(event, slick) {},
    getUrlDomicilios: helperConfig.getUrlDomicilios,
    replaceByDefault(e) {
      e.target.src = require('../../assets/placeholder.png')
    },
    fixUrl(logo) {
      let url = this.getUrlDomicilios() + logo
      return url
    },
    selectDomicilio(domicilio) {
      this.$store.state.domicilioSelected = domicilio
    },
    Back() {
      const back = this.$refs.domicilios.getPreviousPage()
      this.$refs.domicilios.goToPage(back)
    },
    Next() {
      const next = this.$refs.domicilios.getNextPage()
      this.$refs.domicilios.goToPage(next)
    },
    renderImage(name) {
      let image
      if (name === 'Rappi') {
        image = require('../../assets/logo-rappi.png')
      } else if (name === 'Domicilios') {
        image = require('../../assets/logo-domicilios.png')
      }
      return image
    }
  },
  computed: {
    getDomicilios() {
      return this.$store.state.domicilios//.filter(domicilio => domicilio.active)
    }
  }
}
</script>

