<template>
  <div class="categories">
    <div class="flex-categories">
      <div class="icon-categories">
        <font-awesome-icon
          icon="caret-left"
          :style="{ color: '#aaa', cursor: 'pointer'}"
          size="3x"
          @click="Back()"
        />
      </div>
      <div class="icon-categories">
        <font-awesome-icon
          icon="caret-right"
          :style="{ color: '#aaa', cursor: 'pointer'}"
          size="3x"
          @click="Next()"
        />
      </div>
    </div>
    <carousel :perPage="4" :paginationEnabled="false" ref="test">
      <slide v-for="(categorie, index) in categories" :key="index">
        <div
          :class="{categorie: true, 'active-categorie': selected === categorie.id}"
          @click="selectCategorie(categorie)"
        >{{fixName(categorie.name.toUpperCase())}}</div>
      </slide>
    </carousel>
  </div>
</template>
<script>
import axios from "axios";
import helperConfig from "../../helpers/config";
export default {
  name: "Categories",
  data() {
    return {
      categories: [],
      selected: -1
    };
  },
  methods: {
    Back() {
      const back = this.$refs.test.getPreviousPage();
      this.$refs.test.goToPage(back);
    },
    Next() {
      const next = this.$refs.test.getNextPage();
      this.$refs.test.goToPage(next);
    },
    fixName(name) {
      return name.length > 10 ? name.substring(0, 10) : name;
    },
    getConfig: helperConfig.getConfig,
    selectCategorie(categorie) {
      this.selected = categorie.id;

      if (categorie.name === "Todos") {
        this.$store.state.products = this.$store.state.allProducts;
      } else {
        this.$store.state.products = this.$store.state.allProducts.filter(
          function(a) {
            var auxId = a.category ? a.category.id : -2;
            if (auxId === categorie.id) return a;
          }
        );
      }
    },
    getCategoriesLocal() {
      let categories = localStorage.getItem("categories");

      if (categories) {
        categories = JSON.parse(categories);
        this.$store.state.categories = categories;
        this.categories = categories;
        this.isLoading = false;
      }
    },
    async getCategories() {
      const todos = {
        active: 1,
        id: -1,
        image: "",
        name: "Todos",
        shop: 1,
        subcategories: []
      };
      try {
        const res = await axios(this.getConfig("get", "/categories"));
        let data = res.data.data;
        data.unshift(todos);
        this.$store.state.categories = data;
        this.categories = data;
        localStorage.setItem("categories", JSON.stringify(data));
        this.isLoading = false;
      } catch (error) {
        //
      }
    }
  },
  mounted() {
    this.getCategoriesLocal();
    this.getCategories();
  }
};
</script>

<style scoped>
.icon-categories {
  cursor: pointer;
  z-index: 1;
}
.categories {
  position: relative;
  border-bottom: 1px solid #eeeeee;
  border-top: 1px solid #eeeeee;
  padding-top: 1em;
  padding-bottom: 0.8em;
}
.flex-categories {
  width: 100%;
  position: absolute;
  display: flex;
  justify-content: space-between;
}
.active-categorie {
  background-color: #61ce70;
  color: #fff;
}
.categorie {
  cursor: pointer;
  margin: 0.5em 2em 0.5em 2em;
  padding: 0.5em 1em;
  border-radius: 0.5em;
  border: 1px solid #ddd;
  font-size: 0.9em;
}
</style>
