<template>
  <div class="listProduct">
    <ModalAdiciones />
    <div v-for="(items, index1) in Partition($store.state.products, 4)" :key="index1" class="flex">
      <div v-for="(item, index2) in items" :key="index2" class="width30">
        <Product :product="item" />
      </div>
    </div>
    <loading :active.sync="isLoading"></loading>
  </div>
</template>

<script>
// @ is an alias to /src
import axios from "axios";
import Loading from "vue-loading-overlay";
import helperConfig from "../../helpers/config";
import ModalAdiciones from "../ModalAdiciones";
import Product from "./Product.vue";
import "vue-loading-overlay/dist/vue-loading.css";

export default {
  name: "listProduct",
  components: {
    Product,
    ModalAdiciones,
    Loading
  },
  data() {
    return {
      isLoading: true
    };
  },
  methods: {
    getProductsLocal() {
      let products = localStorage.getItem("products");

      if (products) {
        products = JSON.parse(products);
        this.$store.state.products = products;
        this.$store.state.allProducts = products;
        this.isLoading = false;
      }
    },
    async getStatusCommand() {
      try {
        const res = await axios(
          this.getConfig("get", "/take-order/order/command/status")
        );
        this.$store.state.statusComanda = res.data.status;
      } catch (error) {
        //
      }
    },
    async getProducts() {
      try {
        const res = await axios(this.getConfig("get", "/products"));
        let data = res.data.data.filter(product => {
          if (product) {
            const {active, category, name, price, type} = product;
            if (
              active &&
              category &&
              category.active &&
              name &&
              price &&
              price.total >= 0 &&
              type !== 'ingrediente'
            ) {
              return product;
            }
          }
        });
        this.$store.state.allProducts = data;
        this.$store.state.products = data;
        localStorage.setItem("products", JSON.stringify(data));
        this.isLoading = false;
      } catch (error) {
        //
      }
    },
    Partition(items, size) {
      let output = [];
      for (let i = 0; i < items.length; i += size) {
        output[output.length] = items.slice(i, i + size);
      }
      return output;
    },
    getConfig: helperConfig.getConfig
  },
  mounted() {
    this.getProductsLocal();
    this.getProducts();
    this.getStatusCommand();
  }
};
</script>
<style scoped>
.listProduct {
  min-height: 82vh;
  height: 82vh;
  overflow-y: scroll;
}

.flex {
  display: flex;
  margin-bottom: 1em;
}

.width30 {
  width: 25%;
  padding: 1%;
}
</style>
