<template>
  <div :class="{product: true, select: isSelect(product.id)}" @click="addProductLocal(product)">
    <div class="number-items" v-if="isSelect(product.id)">{{getNumber(product.id)}}</div>
    <img alt="Vendty" class="image" :src="getImage()" @error="replaceByDefault" />
    <div class="titleContainer border" align-center>
      <p class="title">{{fixName(product.name)}}</p>
    </div>
    <p class="price">{{numberWithCommas(product.price.total)}}</p>
  </div>
</template>
<script>
import helperCommas from "../../helpers/commas";
import { mapMutations } from "vuex";
import _ from "lodash";

export default {
  name: "Product",
  props: ["product"],
  data() {
    return {};
  },
  methods: {
    replaceByDefault(e) {
      e.target.src = require("../../assets/placeholder.png");
    },
    getNumber(id) {
      let found = this.getFound(id);
      return found.price.qty;
    },
    getFound(id) {
      return this.$store.state.ventas.find(a => a.id === id);
    },
    isSelect(id) {
      let found = this.getFound(id);
      return found ? true : false;
    },
    fixName(name) {
      name = name.length > 60 ? name.substring(0, 60) : name;
      name = name.toUpperCase();

      return name;
    },
    getImage() {
      let image = this.product.images[0];
      return image && image !== ""
        ? image
        : require("../../assets/placeholder.png");
    },
    ...mapMutations(["addProduct"]),
    addProductLocal(product) {
      let auxProduct = _.cloneDeep(product);
      auxProduct.statusDiscount = false;
      auxProduct.discountByProduct = {
        value: 0,
        type: "value",
        total: 0
      };
      auxProduct.additions = _.compact(auxProduct.additions);
      auxProduct.modifications = _.compact(auxProduct.modifications);
      this.addProduct(auxProduct);
      if (
        this.$store.state.type_business === "restaurante" &&
        ((product.additions && product.additions.length > 0) ||
          (product.modifications && product.modifications.length > 0))
      ) {
        this.$store.state.productSelected = auxProduct;
        this.$modal.show("modalAdiciones");
      }
    },
    numberWithCommas: helperCommas.numberWithCommas
  },
  mounted() {
    //this.getProducts()
  }
};
</script>

