<template>
  <div class="container-domicilios">
    <div class="container-inputs">
      <div class="columns">
        <div class="column">
          <div class="item">
            <label class="label-item" for="xd2">Nombre Completo</label>
            <v-select
              placeholder="Buscar cliente"
              v-model="$store.state.userSelected"
              :filter-by="filterClients"
              :options="getClientes"
              :style="{
                width: '100%',
                marginTop: '10px',
              }"
            ></v-select>
          </div>
        </div>
      </div>
      <div class="columns">
        <div class="column">
          <div class="item">
            <label class="label-item">Teléfono</label>
            <input placeholder="Teléfono" class="input-item" type="text" v-model="phone" />
          </div>
        </div>
      </div>
      <div class="columns">
        <div class="column">
          <div class="item">
            <label class="label-item">Dirección</label>
            <input placeholder="Dirección" class="input-item" type="text" v-model="address" />
          </div>
        </div>
      </div>
    </div>
    <div class="domicile-selected" v-if="$store.state.domicilioSelected.id">
      <div class="subcontainer">
        <div class="title">{{$store.state.domicilioSelected.description}}</div>
        <img
          :alt="$store.state.domicilioSelected.description"
          class="image"
          :src="fixUrl($store.state.domicilioSelected.logo)"
          @error="replaceByDefault"
        />
        <font-awesome-icon
          icon="times"
          :style="{ 
            position: 'absolute',
            color: 'red',
            cursor: 'pointer',
            paddingTop: '1em',
            right: '-15px',
            top: 0
          }"
          size="lg"
          @click="removeDomicilio()"
        />
      </div>
    </div>
    <div class="buttons">
      <div class="button cancel" @click="goBack()">Cancelar</div>
      <div class="button success" @click="Guardar()">Guardar</div>
    </div>
  </div>
</template>

<script>
import helperConfig from "../../helpers/config";
import helperAlert from "../../helpers/alert";
import axios from "axios";
export default {
  name: "DataClientes",
  data() {
    return {
      phone: "",
      address: "",
    };
  },
  methods: {
    getConfig: helperConfig.getConfig,
    getUrlDomicilios: helperConfig.getUrlDomicilios,
    setAlert: helperAlert.setAlert,
    removeDomicilio() {
      this.$store.state.domicilioSelected = {}
    },
    replaceByDefault(e) {
      e.target.src = require("../../assets/placeholder.png");
    },
    fixUrl(logo) {
      let url = this.getUrlDomicilios() + logo;
      return url;
    },
    goBack() {
      this.$modal.hide("domicilios");
    },
    async saveDomiciliaries(data) {
      try {
        await axios(
          this.getConfig("post", "/take-order/domiciliaries", data)
        );
        this.goBack();
      } catch (error) {
        //
      }
    },
    Validate() {
      return (
        this.$store.state.userSelected !== "" &&
        this.phone !== "" &&
        this.address !== ""
      );
    },
    Guardar() {
      if (!this.Validate()) {
        this.setAlert("Por favor ingrese todos los campos");
        return;
      }
      const userSelected = {
        ...this.$store.state.userSelected,
        phone: this.phone,
        address: this.address,
      };

      this.$store.state.userSelected = userSelected;
      this.goBack();
    },
    filterClients: function(client, label, search) {
      let { business_name, name, nit, phone } = client;
      name = business_name ? business_name : name;
      name = name.toUpperCase();
      phone = phone ? phone : "";
      search = search.toUpperCase();

      return (
        name.indexOf(search) > -1 ||
        nit.indexOf(search) > -1 ||
        phone.indexOf(search) > -1
      );
    }
  },
  computed: {
    getClientes() {
      let clientes = this.$store.state.clientes.filter(function(client) {
        const { business_name, name, nit } = client;

        return (business_name || name) && (nit && nit != 0);
      });

      clientes = clientes.map(function(client) {
        const { business_name, name } = client;
        let valueName = business_name ? business_name : name;
        valueName = valueName.toUpperCase();
        client.label = valueName;

        return client;
      });

      return clientes.sort((a, b) => {
        if (a.name > b.name) {
          return 1;
        }
        if (a.name < b.name) {
          return -1;
        }
        return 0;
      });
    }
  },
  mounted() {
    this.phone = this.$store.state.userSelected
      ? this.$store.state.userSelected.phone
      : null;
    this.address = this.$store.state.userSelected
      ? this.$store.state.userSelected.address
      : null;
  },
  updated() {
    if (this.client !== this.$store.state.userSelected) {
      this.client = this.$store.state.userSelected;
      this.phone = this.$store.state.userSelected
        ? this.$store.state.userSelected.phone
        : null;
      this.address = this.$store.state.userSelected
        ? this.$store.state.userSelected.address
        : null;
    }
  }
};
</script>

