<template>
  <div class="modalAdiciones">
    <modal
      name="modalAdiciones"
      :clickToClose="true"
      :minHeight="350"
      :height="350"
      :width="'80%'"
    >
      <div class="header">
        <font-awesome-icon
          icon="chevron-left"
          :style="{ position: 'relative', zIndex: 10, color: '#aaa', cursor: 'pointer', paddingTop: 10, paddingLeft: 10, paddingRight: 5, marginBottom: '.3em' }"
          size="lg"
          @click="goBack()"
        />
        <div class="p absolute">{{textSelected}}</div>
        <input 
          type="text"
          v-model="newProduct"
          class="input"
          placeholder="Ingrese el nombre del producto"
          v-if="textSelected === 'Modificaciones'"
          v-on:keyup.enter="createModification"
        >
      </div>
      <div class="container-types">
        <div class="container-positions">
          <div class="left" >
            <div v-for="(type, index) in types" :key="index" class="container-type" 
              @click="selectType(type)"
            >{{type}}</div>
          </div>
          <div class="middle">
            <div class="flex" v-for="(items, index) in Partition(selected, 3)" :key="index">
              <div 
                v-for="(item, index2) in items" 
                :key="index2"
                class="item"
                @click="addItem(item)"
              >
                {{ getName(item)}}
              </div>
            </div>
          </div>
          <div class="right" v-if="textSelected === 'Adiciones'">
            <div class="flex" v-for="(items, index) in Partition(itemsSelecteds, 2)" :key="index">
              <div 
                @click="removeItem(item)"
                v-for="(item, index2) in items"
                :key="index2"
                class="item-selecteds"
              >
                <div class="quantity">
                  {{item.qty}}
                </div>
                {{item.name ? item.name : item.nombre}}
              </div>
            </div>
          </div>
          <div class="right" v-if="textSelected === 'Modificaciones'">
            <div class="flex" v-for="(items, index) in Partition(itemsSelectedsModificaciones, 2)" :key="index">
              <div 
                @click="removeItem(item)"
                v-for="(item, index2) in items"
                :key="index2"
                class="item-selecteds"
              >
                <div class="quantity">
                  {{item.qty}}
                </div>
                {{item.name ? item.name : item.nombre}}
              </div>
            </div>
          </div>
        </div>
      </div>
      
    </modal>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import helperVentas from '../../helpers/ventas'
import helperConfig from '../../helpers/config'
import helperAlert from '../../helpers/alert'
import axios from 'axios'
export default {
  name: 'ModalAdiciones',
  data: function data() {
    return {
      types: ["Adiciones", "Modificaciones", "Tamaño (Proximamente)"],
      textSelected: 'Adiciones',
      selected: ['test'],
      itemsSelecteds: [],
      newProduct: '',
      itemsSelectedsModificaciones: []
    };
  },
  watch: {
    productSelected() {
      this.selectType('Adiciones');
    }
  },
  methods: {
    getAdditionsActive: helperVentas.getAdditionsActive,
    getConfig: helperConfig.getConfig,
    setAlert: helperAlert.setAlert,
    ...mapMutations([
      'addAddition',
      'removeAddition'
    ]),
    getName(item) {
      if (item)
        return item.name ? item.name: item.nombre
      return ''
    },
    async createModification(e) {
      let id = this.selected[0].id_producto
      const data = {
        product: id,
        modification: e.target.value
      }
      try {
        const res = await axios(this.getConfig('post', '/take-order/product/modification/create', data))
        const dataResponse = res.data
        if (dataResponse) {
          let auxAllProducts = this.$store.state.allProducts
          let auxProducts = this.$store.state.products
          let indexAllProducts = auxAllProducts.findIndex((product) => product.id === id)
          let indexProducts = auxProducts.findIndex((product) => product.id === id)
          auxAllProducts[indexAllProducts].modifications.push(dataResponse)
          auxProducts[indexProducts].modifications.push(dataResponse)
          this.selected.push(dataResponse)
          localStorage.setItem('products', JSON.stringify(auxAllProducts))
          this.newProduct = ''
        }
      } catch (error) {
        this.setAlert('La modificacion ya existe o esta ingresando numeros')
      }
      //auxProducts[index].modifications.push()
    },
    Partition(items, size) {
      let output = []
      for (let i = 0; i < items.length; i += size) {
        output[output.length] = items.slice(i, i + size)
      }
      return output
    },
    selectType(type) {
      if (type === 'Adiciones') {
        this.selected = this.productSelected.additions;
        this.textSelected = 'Adiciones';
      } else if (type === 'Modificaciones') {
        this.selected = this.productSelected.modifications;
        this.textSelected = 'Modificaciones';
      }
    },
    addItem(item) {
      if (item.name) {
        //Adiciones
        this.addAddition(item)
        this.itemsSelecteds = this.getAdditionsActive()
      } else {
        let add_aux = Object.assign({}, this.$store.state.productSelected)
        let index = add_aux.modifications.findIndex(function (a) {
          return a.id === item.id
        })
        var modifications = add_aux.modifications
        modifications[index].status = 1
        if (modifications[index].qty) {
          modifications[index].qty += 1
        } else {
          modifications[index].qty = 1
        }
        let ventas = [ ...this.$store.state.ventas];
        ventas.shift()
        this.$store.state.ventas = [
          this.$store.state.productSelected,
          ...ventas
        ]
        this.itemsSelectedsModificaciones = modifications.filter(function (a) {
          return a.status && a.status === 1
        })
      }
    },
    removeItem(item) {
      if (item.name) {
        this.removeAddition(item)
        this.itemsSelecteds = this.getAdditionsActive()
      } else {
        let index = this.$store.state.productSelected.modifications.findIndex(function (a) {
          return a.id === item.id
        });
        let modifications = this.$store.state.productSelected.modifications
        modifications[index].status = 0
        modifications[index].qty = 0
        let id = this.$store.state.productSelected.id
        this.$store.state.ventas = [
          ...this.$store.state.ventas.filter(function (element) {
            return element.id !== id
          }),
          this.$store.state.productSelected
        ]
        this.itemsSelectedsModificaciones = modifications.filter(function (a) {
          return a.status && a.status === 1
        })
      }
    },
    goBack() {
      this.itemsSelecteds = [];
      this.itemsSelectedsModificaciones = [];
      this.$modal.hide('modalAdiciones');
    }
  },
  computed: {
    ...mapState([
      'productSelected'
    ])
  },
  mounted() {}
}
</script>

