<template>
  <div class="methods">
    <carousel :perPage="4" :paginationEnabled="false" class="carousel" ref="test">
      <slide v-for="(method, index) in methods" :key="index" class="slide">
        <div class="item-container" @click="selectMethod(method)">
          <p class="title">{{method.name}}</p>
        </div>
      </slide>
    </carousel>
  </div>
</template>
<script>
import helperAlert from "../../../helpers/alert";
import helperConfig from "../../../helpers/config";
import helperVentas from "../../../helpers/ventas";
import { mapMutations } from "vuex";
import axios from "axios";
export default {
  name: "Methods",
  props: ["input"],
  data() {
    return {
      methods: []
    };
  },
  methods: {
    getConfig: helperConfig.getConfig,
    getRestante: helperVentas.getRestante,
    setAlert: helperAlert.setAlert,
    ...mapMutations(["addCollection"]),
    selectMethod(item) {
      this.input.$el.select();
      if (parseFloat(this.$store.state.totalModalpago) > 0) {
        let restante = this.getRestante();
        if (
          this.$store.state.totalModalpago > restante &&
          item.code !== "efectivo"
        ) {
          this.setAlert("Monto no permitido");
        } else {
          this.addCollection({
            total: this.$store.state.totalModalpago,
            bill: this.$store.state.totalModalpago,
            method: item
          });
          const newTotal = restante - this.$store.state.totalModalpago;
          this.$store.state.totalModalpago = newTotal > 0 ? newTotal : 0;
          //this.props.setTotalPago(newTotal > 0 ? newTotal: 0)
        }
      } else {
        this.setAlert("Total invalido");
      }
    },
    async getMethods() {
      try {
        const not_payments = [
          "Credito",
          "Gift_Card",
          "nota_credito",
          "Puntos",
          "Mastercard",
          "VISA",
          "Amex"
        ];
        const res = await axios(this.getConfig("get", "/payment-methods"));
        this.methods = res.data.data.filter(function (method) {
          return !not_payments.includes(method.code);
        });
        localStorage.setItem("payment-methods", JSON.stringify(this.methods));
      } catch (error) {}
    },
    getLocalMethods() {
      let local = localStorage.getItem("payment-methods");

      if (local) {
        this.methods = JSON.parse(local);
      } else {
        this.getMethods();
      }
    }
  },
  mounted() {
    this.getLocalMethods();
  }
};
</script>

