import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    data_currency: {
      decimals: 0,
      decimals_sep: '.',
      round: false,
      symbol: '$',
      thousands_sep: ','
    },
    domicilioSelected: {},
    domicilios: [],
    token: '',
    groups_customers: [],
    allProducts: [],
    propina: {
      type: 'percent',
      total: 0,
      status: false,
      value: 0
    },
    discount: {
      total: 0,
      value: 0,
      type: 'value',
      status: false
    },
    clientes: [],
    products: [],
    categories: [],
    ventas: [],
    currency: {},
    descuentoVuex: 0,
    type_business: '',
    user: {},
    warehouse: {},
    total: 0,
    totalModalpago: 0,
    collection: [],
    productSelected: -1,
    alert: {
      typeAlert: 'success',
      msgAlert: '',
      statusAlert: false
    },
    statusComanda: false,
    printSystem: false,
    urlSystem: '',
    userSelected: '',
    userLealSelected: null
  },
  getters: {
    getTotalModalPago: state => {
      return state.totalModalpago
    }
  },
  mutations: {
    addDiscount: (state, action) => {
      let ventas = state.ventas.slice()
      let x = ventas.map((a) => {
        let b = Object.assign({}, a.price)
        let add = a.additions
        // if (b.tax > 0) {
        let discount = 0
        if (action.type === 'percent') {
          discount = (action.discount / 100) * b.subtotal
        } else {
          discount = action.discount
        }
        let tax = (b.tax_percent / 100)
        b.subtotal_discount = b.subtotal - discount
        b.newTax = tax * b.subtotal_discount

        let c = Object.assign({}, a)
        c.price = b
        if (add) {

          add.map((d) => {
            d.discount = (action.discount / 100) * d.price
            discount += d.status === 1 && d.discount ? (d.discount * d.qty) : 0
          })
        }
        c.additions = add
        c.discount = discount + (b.tax - b.newTax)
        state.discount.total += c.discount
        return c
        // } else {
        //   b.subtotal_discount = b.subtotal
        //   let c = Object.assign({}, a)
        //   c.price = b
        //   return c
        // }
      })
      state.ventas = x
    },
    discountProduct: (state, action) => {
      if (state.ventas.length > 0) {
        const index = state.ventas.findIndex(item => item.id === action.id)
        const found = state.ventas.find((item) => {
          if (item.additions && item.additions.length > 0) {
            let status = item.additions.find(b => b.status && b.status === 1)
            if (status)
              return true
            else
              return false
          }
        })
        if (index >= 0 && !found) {
          let auxPayload = Object.assign({}, action.price)
          let auxState = Object.assign({}, state.ventas[index].price)
          if (auxState.qty === 1) {
            return
          }
          auxState.qty -= 1
          auxState.total = auxPayload.total * auxState.qty
          auxState.subtotal = auxPayload.subtotal * auxState.qty
          auxState.tax = auxPayload.tax * auxState.qty
          const updatedItems = state.ventas.map(item => {
            if (item.id === action.id) {
              action.price = auxState
              return { ...item, ...action }
            }
            return item
          })
          state.ventas = updatedItems
          return
        }
      }
      const newObject = action
      newObject.price.qty = 1
      state.ventas = [
        newObject,
        ...state.ventas
      ]
    },
    addProduct: (state, action) => {
      const newObject = action
      newObject.price.qty = 1
      state.ventas = [
        newObject,
        ...state.ventas
      ]
    },
    incrementProduct: (state, action) => {
      const index = action.index
      const id = action.id
      const product = state.allProducts.find(a => a.id === id)
      state.ventas[index].price.qty += 1
      let qty = state.ventas[index].price.qty
      state.ventas[index].price.total = product.price.total * qty
      state.ventas[index].price.subtotal = product.price.subtotal * qty
      state.ventas[index].price.tax = product.price.tax * qty
    },
    decrementProduct: (state, action) => {
      const index = action.index
      const id = action.id
      if (state.ventas[index].price.qty > 1) {
        const product = state.allProducts.find(a => a.id === id)
        state.ventas[index].price.qty -= 1
        let qty = state.ventas[index].price.qty
        state.ventas[index].price.total = product.price.total * qty
        state.ventas[index].price.subtotal = product.price.subtotal * qty
        state.ventas[index].price.tax = product.price.tax * qty
      }
    },
    addAddition: (state, action) => {
      let index = state.ventas[0].additions.findIndex((a) => a.id === action.id)
      let aux_adds = Object.assign({}, state.ventas[0])
      aux_adds.additions[index].status = 1
      aux_adds.additions[index].qty = aux_adds.additions[index].qty ? aux_adds.additions[index].qty + 1 : 1
      state.ventas = [
        aux_adds,
        ...state.ventas.filter((a, i) => i !== 0)
      ]
    },
    removeAddition: (state, action) => {
      let index = state.ventas[0].additions.findIndex((a) => a.id === action.id)
      let aux_adds = Object.assign({}, state.ventas[0])
      aux_adds.additions[index].qty = aux_adds.additions[index].qty && aux_adds.additions[index].qty > 0 ? aux_adds.additions[index].qty - 1 : 0
      if (aux_adds.additions[index].qty < 1) {
        aux_adds.additions[index].status = 0
      }
      state.ventas = [
        ...state.ventas.filter((a, i) => i !== 0),
        aux_adds
      ]
    },
    deleteProduct: (state, action) => {
      state.ventas = state.ventas.filter((a, index) => {
        if (index === action.index) {
          if (a && a.additions) {
            a.additions.map((addition) => {
              addition.qty = 0
              addition.status = 0
              addition.discount = 0
            })
          }
          if (a && a.modifications) {
            a.modifications.map((modification) => {
              modification.qty = 0
              modification.status = 0
              modification.discount = 0
            })
          }
        }
        return index !== action.index
      })
    },
    deleteAllProducts: (state) => {
      state.ventas = []
    },
    addCollection: (state, action) => {
      const newObject = {
        total: action.total,
        bill: action.bill,
        method: action.method
      }
      let newCollection = []
      if (state.collection.length > 0 && state.collection.find((a) => a.method.name === action.method.name)) {
        let sum = state.collection.map((a) => {
          if (a.method.name === action.method.name) {
            a.bill += action.bill
            a.total += action.total
            return a
          } else {
            return a
          }
        })
        newCollection = [...sum]
      } else {
        newCollection = [
          ...state.collection,
          newObject
        ]
      }
      state.collection = newCollection
    },
    deleteCollection: (state, action) => {
      state.collection = [
        ...state.collection.filter(a => a.method.name !== action.method.name)
      ]
    },
    userLealSelected: (state, action) => {
      state.userLealSelected = action
    },
  },
  actions: {

  }
})
