<template>
  <modal
    :clickToClose="false"
    name="discount-by-product"
    :minHeight="500"
    :height="500"
    :width="400"
  >
   <div class="header">
      <font-awesome-icon 
        icon="times" 
        :style="{ position: 'absolute', color: '#666161', cursor: 'pointer', right: 0, paddingRight: 10 }" 
        size="lg"
        @click="goBack()"
      />
      Descuento por producto
    </div>
      <div class="container">
        <div class="modelDescuento">
          {{getValue()}}
        </div>
        <div @click="changeType()" class="percentage mr" :class="{select: !isValue}" style="margin-left: 1em;">
          <font-awesome-icon
            icon="percentage"
            :style="{ color: !isValue ? '#fff': '#aaa'}"
            size="lg"
          />
        </div>
        <div @click="changeType()" class="percentage pad" :class="{select: isValue}">
          <font-awesome-icon
            icon="dollar-sign"
            :style="{ color: isValue ? '#fff': '#aaa'}"
            size="lg"
          />  
        </div>
      </div>

      <div class="container2">
        <div class="container-calculator">
          <CalculatorDiscountByProduct :index="index" :type="isValue"/>
          <div class="boton" @click="Guardar(true)" v-if="!($store.state.ventas[index] && $store.state.ventas[index].statusDiscount)">
            <div class="pagar">Guadar descuento</div>
          </div>
          <div class="boton red" @click="Guardar(false)" v-else>
            <div class="pagar">Quitar descuento</div>
          </div>
        </div>
      </div>
  </modal>
</template>

<script>
import CalculatorDiscountByProduct from './CalculatorDiscountByProduct'
import helperCommas from '../../helpers/commas'
export default {
  name: 'DescuentoByProduct',
  components: {CalculatorDiscountByProduct},
  props: ['index'],
  data: function data() {
    return {
      isValue: false
    };
  },
  watch: {
    index(value) {
      
    },
    isValue(value) {
      if(!value) {
        if (this.$store.state.ventas[this.index].discountByProduct.value > 100)
          this.$store.state.ventas[this.index].discountByProduct.value = 0
      }
    }
  },
  methods: {
    numberWithCommas: helperCommas.numberWithCommas,
    getValue() {
      let value =  this.$store.state.ventas[this.index] ? this.$store.state.ventas[this.index].discountByProduct: false
      return this.numberWithCommas(value ? value.value : 0)
    },
    changeType() {
      this.isValue = !this.isValue
      this.$store.state.ventas[this.index].type = this.isValue ? 'value': 'percent'
    },
    goBack() {
      this.$modal.hide('discount-by-product')
    },
    Guardar(status) {
      this.$store.state.ventas[this.index].discountByProduct.type = this.isValue ? 'value': 'percent'
      this.$store.state.ventas[this.index].statusDiscount = status
      this.goBack()
    }
  }
}
</script>

