<template>
  <div class="home">
    <ModalAlert />
    <div class="flex">
      <div class="width60">
        <SearchProduct />
        <Categories />
        <ListProduct />
      </div>
      <div class="width40">
        <Clientes />
        <TableProduct />
      </div>
    </div>
    <div class="flex"></div>
  </div>
</template>

<script>
// @ is an alias to /src
//import HelloWorld from '@/components/HelloWorld.vue'
import SearchProduct from "@/components/SearchProduct";
import ListProduct from "@/components/ListProducts/ListProducts";
import Categories from "@/components/Categories";
import TableProduct from "@/components/TableProducts";
import Clientes from "@/components/Clientes";
import ModalAlert from "@/components/ModalAlert";

import axios from "axios";
import auxAxios from "../helpers/config";

export default {
  name: "home",
  components: {
    ListProduct,
    TableProduct,
    Categories,
    SearchProduct,
    Clientes,
    ModalAlert
  },
  data() {
    return {};
  },
  methods: {
    async getCustomers() {
      try {
        const res = await axios(this.getConfig("get", "/customers"));
        let data = res.data.data;
        if (data) {
          this.$store.state.clientes = data;
          localStorage.setItem("clientes", JSON.stringify(res.data));
        }
      } catch (error) {
        //
      }
    },
    getDataCurrencyLocal() {
      let data_currency = localStorage.getItem("data-currency");

      if (data_currency) {
        data_currency = JSON.parse(data_currency);
        this.$store.state.data_currency = data_currency;
      }
    },
    async getDataCurrency() {
      let { data_currency } = this.$store.state;

      try {
        const res = await axios(this.getConfig("get", "/data-currency"));
        let data = res.data;
        if (data) {
          data_currency = { ...data_currency, ...data };
        }
      } catch (error) {
        //
      }

      this.$store.state.data_currency = data_currency;
      localStorage.setItem("data-currency", JSON.stringify(data_currency));
    },
    getConfig: auxAxios.getConfig,
  },
  async created() {
    let item = localStorage.getItem("api_auth");

    if (item) {
      let auxItem = JSON.parse(item);
      this.$store.state.statusComanda = auxItem.command;
      this.$store.state.puntos_leal = auxItem.puntos_leal;
      this.$store.state.token = auxItem.token;
      this.$store.state.type_business = auxItem.type_business;
      this.$store.state.user = auxItem.user;
      this.$store.state.warehouse = auxItem.warehouse;
      this.getCustomers();
      this.getDataCurrencyLocal();
      this.getDataCurrency();
      localStorage.setItem("apiToken", auxItem.token);
    }
  }
};
</script>

<style scoped>
.flex {
  display: flex;
  width: 100%;
}

.width60 {
  width: 60%;
}
.width40 {
  width: 40%;
}
</style>
