import store from '../store'
import commas from './commas'

export default {
  getPropina: () => getPropina(),
  cleanPropina: () => cleanPropina(),
  cleanDiscount: () => cleanDiscount(),
  getPayments: () => getPayments(),
  getRecibido: () => getRecibido(),
  getRestante: () => getRestante(),
  cleanAll: () => cleanAll(),
  getProducts: () => getProducts(),
  getTotalProducts: () => getTotalProducts(),
  getSubTotal: () => getSubTotal(),
  getDescuento: () => getDescuento(),
  getIva: () => getIva(),
  getIdProducts: () => getIdProducts(),
  getTotalFixed: () => getTotalFixed(),
  getAdditionsActive: () => getAdditionsActive(),
  getStatusDiscountByProduct: () => getStatusDiscountByProduct(),
  getStatusDiscountGlobal: () => getStatusDiscountGlobal(),
  getTotalByProduct: (product) => getTotalByProduct(product),
}

const getTotalProducts = () => {
  let length = 0
  const ventas = store.state.ventas
  for (let i = 0; i < ventas.length; i++) {
    const element = ventas[i]
    length += element.price.qty
  }
  return length
}

const getTotalWithDiscount = () => {
  let totalGeneral = 0
  store.state.ventas.map((product) => {
    let {price, price2} = product
    let {qty = 0} = price
    let {total = 0} = price2
    let {status = false, type = "percent", value = 0} = store.state.discount
    total = commas.numberFormat(total)
    
    product.additions.map((b) => {
      total += b.status == 1 ? commas.numberFormat(b.price * b.qty) : 0
    })

    total = status && type === "percent" ?  commas.numberFormat(total * (1 - (value / 100))) : total
    total *= qty
    totalGeneral += total
  })

  return commas.numberFormat(totalGeneral)
}

const getTotalFixed = () => {
  let total = getTotalWithDiscount() + getPropina()
  return commas.numberFormat(total)
}

const getSubTotal = () => {
  let {status = false, type = "percent", value = 0} = store.state.discount
  let subtotalGeneral = 0
  store.state.ventas.map((product) => {
    let {price, price2} = product
    let {qty = 0} = price
    let {subtotal = 0} = price2
    subtotal = commas.numberFormat(subtotal)
    
    product.additions.map((b) => {
      subtotal += b.status == 1 ? commas.numberFormat(b.subtotal * b.qty) : 0
    })
    subtotal = status && type === "percent" ? commas.numberFormat(subtotal * (1 - (value / 100))) : subtotal
    subtotal *= qty
    subtotalGeneral += subtotal
  })

  return commas.numberFormat(subtotalGeneral)
}

const getIdProducts = () => {
  let x = []
  store.state.ventas.map((a) => {
    if (a) {
      let adds = a.additions && a.additions.length > 0 ? a.additions.filter(c => c.status === 1) : []
      let ids = []
      for (let i = 0; i < adds.length; i++) {
        const element = adds[i]
        if (element.qty) {
          for (let i = 0; i < element.qty; i++) {
            ids.push(element.id)
          }
        } else {
          ids = [element.id]
        }
      }
      let mods = a.modifications && a.modifications.length > 0 ? a.modifications.filter(c => c.status === 1) : []
      let ids2 = []
      for (let i = 0; i < mods.length; i++) {
        const element = mods[i]
        if (element.qty) {
          for (let i = 0; i < element.qty; i++) {
            ids2.push(element.id)
          }
        } else {
          ids2 = [element.id]
        }
      }

      let obj = {
        id: a.id,
        additions: ids,
        modifications: ids2
      }
      let qty = a.price.qty ? a.price.qty : 1
      for (let i = 0; i < qty; i++) {
        x.push(obj)
      }
    }
  })
  return x
}

const getIva = () => {
  let {status = false, type = "percent", value = 0} = store.state.discount
  let taxGeneral = 0
  store.state.ventas.map((product) => {
    let {price, price2} = product
    let {qty = 0} = price
    let {tax = 0} = price2
    tax = commas.numberFormat(tax)
    
    product.additions.map((b) => {
      tax += b.status == 1 ? commas.numberFormat(b.tax * b.qty) : 0
    })

    tax = status && type === "percent" ? commas.numberFormat(tax * (1 - (value / 100))) : tax
    tax *= qty
    taxGeneral += tax
  })

  return commas.numberFormat(taxGeneral)
}

const getDescuento = () => {
  let {status = false, type = "percent", value = 0} = store.state.discount
  let discountGeneral = 0
  if (status) {
    store.state.ventas.map((product) => {
      let {price, price2} = product
      let {qty = 0} = price
      let {total = 0} = price2
      let discount  = 0
      total = commas.numberFormat(total)
      
      product.additions.map((b) => {
        total += b.status == 1 ? commas.numberFormat(b.price * b.qty) : 0
      })

      discount = status && type === "percent" ? commas.numberFormat((total / 100) * value) : total
      discount *= qty
      discountGeneral += discount
    })
  }

  return commas.numberFormat(discountGeneral)
}

const getRecibido = () => {
  const auxTotal = store.state.collection.map((a) => a.total)
  let total = 0
  if (auxTotal.length > 0) {
    total = auxTotal.reduce((a, b) => parseFloat(a) + parseFloat(b))
  }
  return total
}

const getProducts = () => {
  let total = store.state.ventas.map((product) => {
    const id = product.id
    const name = product.name.toUpperCase()
    const quantity = product.price.qty
    const additions = product.additions.filter((addition) => addition.status === 1)
    const modifications = product.modifications.filter((modification) => modification.status === 1)

    return { id, name, quantity, additions, modifications }
  })

  return total
}

const cleanAll = () => {
  store.state.collection = []
  store.state.total = 0
  store.state.totalModalpago = 0
  store.state.ventas.map((a) => {
    if (a && a.additions) {
      a.additions.map((addition) => {
        addition.qty = 0
        addition.status = 0
        addition.discount = 0
      })
    }
    if (a && a.modifications) {
      a.modifications.map((modification) => {
        modification.qty = 0
        modification.status = 0
        modification.discount = 0
      })
    }
  })
  store.state.domicilioSelected = {}
  store.state.userLealSelected = null
  store.state.userSelected = ''
  store.state.ventas = []
  cleanDiscount()
  cleanPropina()
}

const cleanPropina = () => {
  store.state.propina.value = 0
  store.state.propina.status = false
  store.state.propina.total = 0
}

const cleanDiscount = () => {
  let discount = store.state.discount
  discount.value = 0
  discount.type = "value"
  discount.total = 0
  discount.status = false

}

const getRestante = () => {
  const total = store.state.total
  let restante = getRecibido()
  let x = total - restante
  return x
}

const getPayments = () => {
  let payments = []
  for (let i = 0; i < store.state.collection.length; i++) {
    const element = store.state.collection[i];
    payments.push({
      id: element.method.id,
      type_payment: element.method.code,
      total_payment_method: element.bill
    })
  }
  return payments
}

const getPropina = () => {
  let {status = false, type = 'percent', value = 0} = store.state.propina
  let propina = 0

  if (status) {
    let total = getTotalWithDiscount()
    propina = type === 'percent' ? (total * (value / 100)) : value
  }
  
  return commas.numberFormat(propina)
}

const getAdditionsActive = () => {
  return store.state.ventas[0].additions.filter((a) => a && a.status && a.status === 1)
}

const getStatusDiscountByProduct = () => {
  return store.state.ventas.find(product => product.statusDiscount)
}

const getStatusDiscountGlobal = () => {
  return store.state.discount.status
}

const getTotalByProduct = (product) => {
  // let discount = 0
  // let {discountByProduct, price = {}, statusDiscount = false} = product
  let {price, price2} = product
  let {qty = 0} = price
  let {total = 0} = price2
  let {status = false, type = "percent", value = 0} = store.state.discount
  total = commas.numberFormat(total)
  
  product.additions.map((b) => {
    total += b.status == 1 ? commas.numberFormat(b.price * b.qty) : 0
  })

  // if (statusDiscount) {
  //   discount = discountByProduct.type === "value" ? discountByProduct.value : (total * (discountByProduct.value / 100))
  // }

  // total -= discount
  total = status && type === "percent" ? commas.numberFormat(total * (1 - (value / 100))) : total
  total *= qty

  return commas.numberFormat(total)
}
