<template>
  <div class="bills">
    <div v-for="(items, index1) in Partition(bills, 2)" :key="index1" class="flex">
      <div
        v-for="(bill, index2) in items"
        :key="index2"
        class="bill"
        @click="selectBill(bill)"
      >{{numberWithCommas(bill)}}</div>
    </div>
  </div>
</template>
<script>
import helperCommas from "../../../helpers/commas";
import helperConfig from "../../../helpers/config";
import axios from "axios";

export default {
  name: "Bills",
  props: ["input"],
  data() {
    return {
      bills: []
    };
  },
  methods: {
    numberWithCommas: helperCommas.numberWithCommas,
    getConfig: helperConfig.getConfig,
    selectBill(bill) {
      this.input.$el.select();
      this.$store.state.totalModalpago = parseFloat(bill);
    },
    async getBills() {
      let data = {
        amount: this.$store.state.totalModalPago
      };
      try {
        const res = await axios(
          this.getConfig("post", "/checkout/help-pay-amounts", data)
        );
        this.bills = res.data;
        localStorage.setItem("bills", JSON.stringify(this.bills));
      } catch (error) {
        //
      }
    },
    getLocalBills() {
      let local = localStorage.getItem("bills");
      if (local) {
        this.bills = JSON.parse(local);
      } else {
        this.getBills();
      }
    },
    Partition(items, size) {
      let output = [];
      for (let i = 0; i < items.length; i += size) {
        output[output.length] = items.slice(i, i + size);
      }
      return output;
    }
  },
  mounted() {
    this.getLocalBills();
  }
};
</script>

<style scoped>
.flex {
  display: flex;
  justify-content: space-between;
}

.bills {
  border-radius: 5px;
  border: 1px solid #ccc;
  padding: 1em;
  background-color: #fff;
  height: 45.5vh;
  min-height: 45.5vh;
}

.bill {
  cursor: pointer;
  margin-bottom: 0.5em;
  padding: 0.8em 0;
  border-radius: 5px;
  font-size: 0.9em;
  border: 1px solid #ccc;
  width: 45%;
  text-align: center;
}
</style>
