<template>
  <div class="clientes">
    <v-select
      placeholder="Buscar cliente"
      v-model="$store.state.userSelected"
      :filter-by="filterClients"
      :options="getClientes"
      :style="{
        width: '94%',
        paddingLeft: '1em',
        marginBotton: '1em',
        borderRadius: '50%!important',
        color: '#ddd' 
      }"
    ></v-select>
    <div class="float" @click="openModal">+</div>
    <AddCliente />
  </div>
</template>

<script>
import AddCliente from "./addCliente";
export default {
  name: "Clientes",
  components: {
    AddCliente
  },
  data() {
    return {
      clientes: []
    };
  },
  methods: {
    openModal() {
      this.$modal.show("addCliente");
    },
    filterClients: function(client, label, search) {
      let { business_name, name, nit, phone } = client;
      name = business_name ? business_name : name;
      name = name.toUpperCase();
      phone = phone ? phone : "";
      search = search.toUpperCase();

      return (
        name.indexOf(search) > -1 ||
        nit.indexOf(search) > -1 ||
        phone.indexOf(search) > -1
      );
    }
  },
  computed: {
    getClientes() {
      let clientes = this.$store.state.clientes.filter(function(client) {
        const { business_name, name, nit } = client;

        return (business_name || name) && (nit && nit != 0);
      });

      clientes = clientes.map(function(client) {
        const { business_name, name, nit } = client;
        let valueName = business_name ? business_name : name;
        valueName = valueName.toUpperCase();
        client.label = `${nit} - ${valueName}`;

        return client;
      });

      return clientes.sort((a, b) => {
        if (a.name > b.name) {
          return 1;
        }
        if (a.name < b.name) {
          return -1;
        }
        return 0;
      });
    }
  }
};
</script>

<style scoped>
.clientes {
  display: flex;
  position: relative;
  border-left: 1px solid #ddd;
  padding: 0.5em 0px;
}

.float {
  margin-left: 1em;
  cursor: pointer;
  border-radius: 50%;
  border: 1px solid green;
  color: green;
  height: 18px;
  padding: 0 0.3em;
  margin-top: 0.5em;
}
</style>

