<template>
  <div class="descuento">
    <modal
      name="propina"
      :clickToClose="false"
      :minHeight="500"
      :height="500"
      :width="400"
    >
      <font-awesome-icon
        icon="times"
        :style="{ position: 'absolute', color: '#fff', cursor: 'pointer', paddingTop: 10, paddingRight: 15, right: 0 }"
        size="lg"
        @click="goBack()"
      />
      <div class="header">
        <div class="title">PROPINA</div>
      </div>
      <div class="container">
        <div class="modelDescuento">
          {{!isValue ? $store.state.propina.value : numberWithCommas($store.state.propina.value)}}
        </div>
        <div @click="changeType()" class="percentage mr" :class="{select: !isValue}">
          <font-awesome-icon
            icon="percentage"
            :style="{ color: !isValue ? '#fff': '#aaa'}"
            size="lg"
          />
        </div>
        <div @click="changeType()" class="percentage pad" :class="{select: isValue}">
          <font-awesome-icon
            icon="dollar-sign"
            :style="{ color: isValue ? '#fff': '#aaa'}"
            size="lg"
          />  
        </div>
      </div>

      <div class="container2">
        <div class="container-calculator">
          <Calculator :isValue="isValue"/>
          <div class="boton" @click="Guardar">
            <div class="pagar">Guadar Propina</div>
          </div>
        </div>
      </div>
    </modal>

  </div>
</template>

<script>
import helperConfig from "../../helpers/config"
import helperCommas from "../../helpers/commas"
import Calculator from "./Calculator"
export default {
  name: 'Propina',
  components: {
    Calculator
  },
  data: function data() {
    return {
      propina: '',
      isValue: false
    };
  },
  watch: {
    isValue(value) {
      if(!value && this.$store.state.propina.value > 100) {
        this.$store.state.propina.value = 0
      }
    }
  },
  methods: {
    getConfig: helperConfig.getConfig,
    numberWithCommas: helperCommas.numberWithCommas,
    goBack() {
      this.$modal.hide('propina');
    },
    changeType() {
      this.isValue = !this.isValue
    },
    Guardar() {
      this.$store.state.propina.status = true
      if (!this.isValue)
        this.$store.state.propina.type = 'percent'
      else
        this.$store.state.propina.type = 'value'
      this.goBack();
    }
  }
}
</script>

