<template>
  <div class="Right">
    <div class="box-white flex-space">
      <label class="total-recibido" for="valor_recibido">Total recibido</label>
      <money
        v-model="$store.state.totalModalpago"
        v-bind="money"
        ref="input"
        class="form-input input-lg total"
      ></money>
      <div></div>
      <div></div>
      <div class="boton leal-container" @click="openPuntosLeal()" v-if="this.$store.state.puntos_leal">
        <img class="pagar" width="30px" :src="require('../../../assets/puntos_leal.png')" />
      </div>
    </div>
    <Methods :input="$refs.input" />
    <div class="columns">
      <div class="column">
        <Left />
      </div>
      <div class="column">
        <Calculator />
      </div>
      <div class="column">
        <Bills :input="$refs.input" />
      </div>
    </div>

    <div class="columns">
      <div class="column"></div>
      <div class="column">
        <div class="buttons">
          <div class="button button-gray" @click="Cancelar()">Cancelar</div>
          <div class="button button-success" @click="Imprimir()">Imprimir</div>
        </div>
      </div>
      <div class="column"></div>
    </div>
  </div>
</template>
<script>
import helperAlert from "../../../helpers/alert";
import helperCommas from "../../../helpers/commas";
import helperConfig from "../../../helpers/config";
import helperVentas from "../../../helpers/ventas";
import { mapGetters } from "vuex";
import axios from "axios";
import Left from "../Left";
import Methods from "./Methods";
import Bills from "./Bills";
import Calculator from "./Calculator";

export default {
  name: "Right",
  components: {
    Methods,
    Bills,
    Left,
    Calculator
  },
  data() {
    return {
      total: 0,
      isLoading: false,
      conn: {},
      money: {}
    };
  },
  methods: {
    sendDataToSocket(data) {
      this.conn.send(data);
    },
    openConnection() {
      //this.conn = window.WebSocket || window.MozWebSocket;
      if (this.conn.readyState === undefined || this.conn.readyState > 1) {
        this.conn = new WebSocket(this.getUrlSocketPrinter());
        var app = this;

        this.conn.onopen = function() {
          app.conn.send("Connection Established Confirmation");
        };

        this.conn.onmessage = function() {
          //document.getElementById("content").innerHTML = event.data;
        };

        this.conn.onerror = function() {
          console.log("Web Socket Error");
          app.$store.state.printSystem = true;
        };

        this.conn.onclose = function() {
          console.log("Web Socket Closed");
        };
      }
    },
    getCambio() {
      const cambio = this.getRestante();
      return cambio > 0 ? 0 : cambio * -1;
    },
    moneyFormat() {
      const { decimals, decimals_sep, symbol, thousands_sep } = this.$store.state.data_currency

      this.money = {
        thousands: thousands_sep,
        decimal: decimals_sep,
        prefix: symbol + ' ',
        precision: parseInt(decimals),
        masked: false
      }
    },
    async Imprimir() {
      let cliente =
        typeof this.$store.state.userSelected === "object"
          ? this.$store.state.userSelected
          : {};
      if (!this.isLoading) {
        this.isLoading = true;
        let recibido = this.getRecibido();

        if (recibido < this.$store.state.total) {
          this.setAlert("Por favor verifique los datos");
          return;
        }

        const payments = this.getPayments();
        const products = this.getProducts();
        const total_propine = this.getPropina();
        const store_id = this.$store.state.warehouse.id;
        const customer = cliente.id ? cliente.id : null;
        const total_payment = this.numberFormat(this.$store.state.total);
        const total_received = this.numberFormat(recibido);
        const total_change = this.getCambio();
        let discount = this.$store.state.discount;
        const note_command = this.$store.state.note;
        this.$store.state.note = "";
        const domicilio = this.$store.state.domicilioSelected;
        const ticket = {
          products,
          payments,
          store_id,
          total_payment,
          total_received,
          total_change: total_change <= 0 ? 0 : this.numberFormat(total_change),
          selected_domicile: domicilio.id ? domicilio.id : null,
          selected_domicile_phone: cliente ? cliente.phone : null,
          selected_domicile_address: cliente ? cliente.address : null,
          customer,
          type_process: "quick-service",
          total_discount: this.numberFormat(discount.total),
          type_discount: discount.type === "value" ? "value" : "percent",
          discount:
            discount.value === "value"
              ? "0"
              : this.numberFormat(discount.value),
          total_propine,
          note_command,
          user_puntos_leal: this.$store.state.userLealSelected
        };

        const command = {
          products,
          zone: "-1",
          note: note_command ? note_command : "",
          table: "Quick Service",
          section: "Quick Service"
        }

        this.Checkout(ticket, command);
      } else {
        this.setAlert("Por favor espere hasta que se procese la compra");
      }
    },
    async Checkout(data, command) {
      try {
        const res = await axios(this.getConfig("post", "/checkout/process", data));
        if (res.data.status) {
          this.isLoading = false;
          let statusModal = res.data.fast_print_new;
          let _data = res.data.fast_print_data_new;
          command.date = _data.venta.fecha;
          command.order = _data.venta.consecutivo_orden;
          _data.comanda = command;
          const propina = _data.venta.details.filter(detail => detail.nombre_producto === 'PROPINA')[0];
          const total_venta = _data.venta.total_venta;
          const total_propina = propina ? propina.precio_venta : 0;
          _data.venta.total_venta = total_venta - total_propina;
          _data = JSON.stringify(_data);
          this.sendDataToSocket(_data);
          this.cleanAll();
          this.$modal.hide("hello-world");
          if (this.$store.state.printSystem && !statusModal) {
            this.$modal.show("system");
            this.$store.state.urlSystem = res.data.id_sale;
          }
        }
      } catch (error) {
        this.cleanAll();
        this.isLoading = false;
        this.$modal.hide("hello-world");
        return false;
      }
    },
    Cancelar() {
      this.isLoading = false;
      this.$modal.hide("hello-world");
    },
    openPuntosLeal() {
      this.$modal.show("leal");
    },
    setAlert: helperAlert.setAlert,
    getConfig: helperConfig.getConfig,
    getUrlSocketPrinter: helperConfig.getUrlSocketPrinter,
    getProducts: helperVentas.getProducts,
    getPropina: helperVentas.getPropina,
    cleanAll: helperVentas.cleanAll,
    getPayments: helperVentas.getPayments,
    getRecibido: helperVentas.getRecibido,
    getRestante: helperVentas.getRestante,
    getIdProducts: helperVentas.getIdProducts,
    numberFormat: helperCommas.numberFormat
  },
  computed: {
    ...mapGetters(["getTotalModalPago"])
  },
  mounted() {
    this.openConnection();
    this.$refs.input.$el.select();
    this.moneyFormat();
  }
};
</script>

