import store from '../store'

export default {
  numberWithCommas: (value) => numberWithCommas(value),
  numberFormat: (value) => numberFormat(value)
}

const numberWithCommas = (value) => {
  const currency = store.state.data_currency
  let { decimals_sep, symbol, thousands_sep } = currency
  value = numberFormat(value, currency)
  value = value.toString().split('.')
  decimals_sep = value[1] ? decimals_sep + value[1] : ''
  value = value[0]

  return symbol + ' ' + value.replace(/\B(?=(\d{3})+(?!\d))/g, thousands_sep) + decimals_sep
}

const numberFormat = (value) => {
  const currency = store.state.data_currency
  let { decimals, round } = currency
  value = round ? Math.round(value) : value
  value = parseFloat(value).toFixed(decimals)

  return parseFloat(value)
}