<template>
  <div class="container-domicilios">
    <div class="item">
      <label class="label-item">Cédula o teléfono</label>
      <div style="display: flex; padding-bottom: 10px">
        <input placeholder="Cédula o teléfono" class="input-item" v-on:keyup.enter="buscar" type="text" v-model="name" />
        <div class="button success" @click="buscar">Buscar</div>
      </div>
      <div v-if="this.$store.state.userLealSelected" class="user">
        <div class="title">
          {{`${$store.state.userLealSelected.cedula} - ${$store.state.userLealSelected.fullname}`}}
        </div>
        <font-awesome-icon
          icon="times"
          :style="{ color: 'red', cursor: 'pointer' }"
          size="lg"
          @click="deseleccionar()"
        />
      </div>
    </div>
    <div class="users">
      <div
        v-for="(user, index) in users"
        :key="index"
        :class="{user: true, 'selected-user': userSelected && userSelected.uid === user.uid}"
        @click="select(user)"
      >
        <div class="title">{{`${user.cedula} - ${user.fullname}`}}</div>
      </div>
    </div>
    <div class="buttons">
      <div class="button cancel" @click="goBack()">Cancelar</div>
      <div class="button success" @click="seleccionar()">Seleccionar</div>
    </div>
    <Loading :active.sync="isLoading"></Loading>
  </div>
</template>

<script>
import helperConfig from "../../helpers/config";
import helperAlert from "../../helpers/alert";
import axios from "axios";
import Loading from "vue-loading-overlay";
import { mapMutations } from "vuex"
export default {
  name: "DataClientes",
  components: {
    Loading
  },
  data() {
    return {
      name: "",
      users: [],
      userSelected: null,
      isLoading: false
    };
  },
  methods: {
    ...mapMutations(["userLealSelected"]),
    getConfig: helperConfig.getConfig,
    setAlert: helperAlert.setAlert,
    goBack() {
      this.$modal.hide("leal");
    },
    select(user) {
      this.userSelected = user;
    },
    deseleccionar() {
      this.userLealSelected(null);
    },
    seleccionar() {
      if (!this.userSelected) {
        this.setAlert("Por favor seleccione a un usuario Leal");
        return;
      }

      this.$store.state.userLealSelected = this.userSelected;
      this.goBack();
    },
    async buscar() {
      if (this.name === "") {
        return;
      }
        
      this.isLoading = true;

      try {
        const res = await axios(this.getConfig("get", `/puntos_leal/search_user/${this.name}`));
        const data = res.data;

        if (data) {
          this.users = data.data;
        }
      } catch (error) {
        this.setAlert("Error al intentar buscar al usuario leal.");
      }

      this.isLoading = false;
    }
  }
};
</script>

